/*
 * @Description: 自定义处理ajax请求，并封装了请求失败后的错误处理
 * @Author: 赵添
 * @LastEditors: 赵添
 * @Date: 2019-03-22 15:41:06
 * @LastEditTime: 2019-04-25 11:21:24
 */
import axios from 'axios'
import { Message, MessageBox } from 'element-ui'

const codeMessage = {
  200: '服务器成功返回请求的数据。',
  201: '新建或修改数据成功。',
  202: '一个请求已经进入后台排队（异步任务）。',
  204: '删除数据成功。',
  400: '发出的请求有错误，服务器没有进行新建或修改数据的操作。',
  401: '用户没有权限（令牌、用户名、密码错误）。',
  403: '用户得到授权，但是访问是被禁止的。',
  404: '发出的请求针对的是不存在的记录，服务器没有进行操作。',
  406: '请求的格式不可得。',
  410: '请求的资源被永久删除，且不会再得到的。',
  422: '当创建一个对象时，发生一个验证错误。',
  500: '服务器发生错误，请检查服务器。',
  502: '网关错误。',
  503: '服务不可用，服务器暂时过载或维护。',
  504: '网关超时。',
};

// eslint-disable-next-line
function $params(obj) {
  const str = [];
  for (const p in obj) {
    str.push(
      encodeURIComponent(p) +
      '=' +
      encodeURIComponent(typeof obj[p] === 'object' ? JSON.stringify(obj[p]) : obj[p])
    );
  }
  return str.join('&');
}
/**
 * @description: 基于axios的自定义请求函数
 * @param options :{methods:请求方式,url：请求地址,headers：自定义头部,data：请求参数,type：请求类型}
 * @return: 请求的promise
 */
export default function request( options, timeout = 10000) {
  return new Promise((resolve, reject) => {
    const headers = {};
    let url = options.url;
    // if (process.env.NODE_ENV === 'production'){
      url = url.replace(/^\/api/,'').replace(/^\/mock/,'');
      url = 'http://sale-game.haoyunqi.com.cn' + url;
    // }
    // console.log(process.env.NODE_ENV ,url);
    const data = {
      method: options.method || 'GET',
      url,
      headers: {
        Accept: 'application/json',
        'Content-Type': options.type === 'json' ? 'application/json; charset=utf-8':'application/x-www-form-urlencoded',
        ...options.headers,
        ...headers
      },
      timeout:timeout
    };
    switch (data.method) {
      case "GET":
        data.params = options.data;
        break;
      case "POST":
        // data.data = $params(options.data);
        data.data = options.type === 'json' ? options.data : $params(options.data);
        break;
    }
    axios(data).then((resp) => {
      if(resp.data.status === 200 || resp.data.code == '200' ){
        resolve(resp.data);
      }else{
        reject(resp);
        if(resp.data){
          Message({
            message: resp.data.message || '未知错误',
            type: 'error',
            duration: 5 * 1000
          })
        }
      }
    }).catch((err) => {
      console.dir(err);
      reject(err);
      if(err.response) {
        if(err.response.status === 401){
          // Toast.info('登录失效请重新登陆');
          MessageBox.confirm(
            '登录失效，可以取消继续留在该页面，或者重新登录',
            '留在本页面',
            {
              confirmButtonText: '重新登录',
              cancelButtonText: '取消',
              type: 'warning'
            }
          )
          return
        }
        const errortext = codeMessage[err.response.status] || '网络异常，请求数据失败,请手动刷新页面';
        Message({
          message: errortext,
          type: 'error',
          duration: 5 * 1000
        })
      }else{
        Message({
          message: '网络异常，请求数据失败,请手动刷新页面',
          type: 'error',
          duration: 5 * 1000
        })
      }
    })
  });
}

